import { newE2EPage } from '@stencil/core/testing';

describe('presto-video', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<presto-video></presto-video>');

    const element = await page.find('presto-video');
    expect(element).toHaveClass('hydrated');
  });
});
